/*
 * SASS interpolation
 */
/*
 * PX to EM
 * @param $px - px value to be converted
 * @param $base - base font size (in `em`)
 * Note: 'em' values are calculate based on the element font-size
 *       to properly converts 'px' to 'em', please pass in the element font-size with it
 * Usage example:
    padding: em(16px, 1.6em); // font-size in 'em'
 */
/*
 * Set property by passing a property name, and values.
 * @param $property name - padding, margin etc.
 * @param $values        - values in `px` (space separated for multiple values)
 * @param $font-size     - base font-size in `em`
 * Usage example:
   @include toEm(padding, 8px 16px 8px, 1.6em);
 */
/*
 * Generate typefaces key-value pair of variable name and config
 * Example:
   --paragraph-center-bold-black: (14px, center, bold, black)
 */
/*
 * Sets font-size, font-weight, color, text-transform, text-align and line-height
 * Usage example:
   @include setTypeface(16px, bold, black, uppercase);
 */
/*
 * Set typefaces by passing a typeface variable name
 * @param $var   - typeface variable
 * @param $tt    - text-transform property
 * @param $align - text-align property
 * Usage example:
   @include typeface(--paragraph-bold-black, uppercase, center);
 */
/*
 * Range Slider Thumb styling is set here
 */
/*
 * SVG icons colors
 */
/*
 * Bar Spinner Animation
 * @param $count    - number of bars
 * @param $duration - duration of animation
 * @param $phase    - each bar phase change delay
 * Usage example:
   @include createBarspinnerAnimation(5, 1.2s, 0,1);
 */
/*
 * Implementation of themes
 * generate sets of themes keys object property (dark / light)
 * @param $themes - themes property
 * Usage example:
   @include themify($themes) { color: themed('background_color') }
 */
/*
 * Generate typefaces key-value pair of themefy object
 * Should be inside themify mixins
 * Example:
   @include themify($themes) { border-color: 1px solid themed('background_color') }
 */
/*
 * Tooltip colors
 * @param $color - color property
 * Usage example:
   @include tooltipColor($COLOR_RED);
 */
/*
 * Themified Tooltip colors
 * @param $color - color property
 * Usage example:
   @include tooltipColor($COLOR_RED);
 */
/*
    Constants
*/
/*
    Variables
*/
/*
    List of all typefaces variables in the format: --$FONT_SIZES-$TEXT_ALIGN-$FONT_WEIGHTS-COLORS.
    See $FONT_SIZES, $TEXT_ALIGN, $FONT_WEIGHTS, $COLORS maps above for references.
    Variables name example: --title-center-semibold-red
 */
/*
    to use styles without modifier class on .inline-icon
    use @extend

    e.g. style icon on parent hover:
        a:hover .inline-icon {
            @extend %inline-icon.active;
        }
*/
.flyout {
  position: absolute;
  left: 250px;
  background-color: #ffffff;
  max-height: calc(100% - 40px);
  z-index: 1;
  border-radius: 10px;
  padding: 25px;
  font-size: 2em;
  margin-left: 15px;
  margin-top: 20px;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.4);
  min-width: 400px; }
  .flyout__content {
    overflow: auto; }
  .flyout__block-workspace {
    margin-bottom: 15px; }
    .flyout__block-workspace * .blocklySvg {
      background-color: inherit !important; }
    .flyout__block-workspace * .blocklyMainBackground {
      stroke: none !important; }
  .flyout__button {
    border: none;
    border-radius: 4px;
    padding: 5px 20px;
    cursor: pointer;
    font-size: 14px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.43;
    letter-spacing: normal;
    text-align: center; }
    .flyout__button-new {
      width: 100%;
      font-size: 100%;
      box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.4);
      margin-bottom: 15px; }
    .flyout__button-add {
      background-color: #ff444f;
      color: #ffffff; }
    .flyout__button-back {
      align-self: center;
      background-color: transparent;
      color: #000000; }
      .flyout__button-back:focus {
        outline: none; }
    .flyout__button-next, .flyout__button-previous {
      margin-left: 1em;
      color: rgba(0, 0, 0, 0.8);
      background-color: #f4f4f6; }
  .flyout__item:not(:last-of-type) {
    margin-bottom: 30px; }
  .flyout__item-header {
    display: flex;
    margin-top: 15px;
    margin-bottom: 10px; }
  .flyout__item-label {
    font-weight: bold; }
  .flyout__item-buttons {
    margin-left: auto;
    display: flex;
    justify-content: flex-end; }
  .flyout__item-info {
    cursor: pointer;
    color: #e31c4b;
    margin-left: 0.2em; }
  .flyout__item-description {
    font-size: 0.8em;
    margin-bottom: 1em; }
  .flyout__image {
    width: 100%;
    height: auto;
    border-radius: 0.5em; }
  .flyout__video {
    width: 100%;
    height: 20vh;
    border-radius: 0.5em; }
  .flyout__help {
    padding: 0; }
    .flyout__help-header {
      padding: 1.2em 1em;
      display: flex;
      background-color: #f4f4f6; }
    .flyout__help-content {
      padding: 1.5em;
      overflow-y: auto;
      height: calc(100vh - 300px); }
      .flyout__help-content .flyout__item {
        margin-bottom: 2em; }
    .flyout__help-title {
      align-self: center;
      font-size: 25px;
      font-weight: bold; }
    .flyout__help-footer {
      text-align: right;
      padding: 1.2em 0.8em;
      border-top: solid 0.1em #f4f4f6; }
